@import url(https://fonts.googleapis.com/css2?family=Cabin+Sketch&display=swap);


* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
  }
  body {
    font-family: "Cabin Sketch"
  }
  
  main {
    min-height: 100vh;
    padding: 25px;
    background-image: linear-gradient(to bottom, rgba(0,0,0, 0.2), rgba(0,0,0, 0.40) );
  }
  
  .wx {
    background-color: rgb(141, 172, 230);
    background-image: url(/weatehrApp/static/media/warmbg.e3bb8573.png);
    background-size: cover;
    background-position: bottom;
    transition: 1s ease-in-out;
  
  }
  
  .wx.cold {
    background-color: rgb(141, 172, 230);
    background-image: url(/weatehrApp/static/media/coldbg.b1d597e3.png); 
    background-size: cover;
    background-position: bottom;
    transition: 1s ease-in-out;
  }

  .wx.rain {
    background-color: rgb(141, 172, 230);
    background-image: url(/weatehrApp/static/media/coldbg.b1d597e3.png); 
    background-size: cover;
    background-position: bottom;
    transition: 1s ease-in-out;
  }

  .wx.clouds {
    background-color: rgb(141, 172, 230);
    background-image: url(/weatehrApp/static/media/cloudsbg.36dd2cfe.png); 
    background-size: cover;
    background-position: bottom;
    transition: 1s ease-in-out;
  }
  

  .searchBox{
      width: 100%;
      margin: 0 0 30px
  }

  .searchBox .searchBar {
      display: block;
      width: 100%;
      padding: 20px;
      -webkit-appearance: none;
              appearance: none;
      background: none;
      border: none;
      outline: none;
      
      border-radius: 10px;
      margin-top: -5px;
      background-color: aliceblue;

      box-shadow: 0px 5px rgba(0, 0, 0, 0.2);

  }
.locationBox{
  text-align: center;
  font-size: 23px;
  color: aquamarine;
  text-shadow: 0px 3px rgba(0, 0, 0, 0.2);
}
.location{
  padding: 5%;
  
}

.weatherBox {
    text-align: center;
    padding-top: 20%;
  }
.temp{
  position: relative;
  margin: 20px auto;
  font-size: 60px;
  color: aquamarine;
  box-shadow: 0px 3px rgba(0, 0, 0, 0.2);
  width: 50%;
  border-radius: 15px;
  background-color: rgba(172, 166, 255, 0.7);
  transition-timing-function: ease-in;
  transition-timing-function: cubic-bezier(0.42, 0, 1, 1);
}
.weather{
  text-align: center;
  font-size: 23px;
  color: aquamarine;
  text-shadow: 0px 3px rgba(0, 0, 0, 0.2);
}
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

